.answer_item{
    margin: 50px;
    input{  
        display: none;    
    }
    input[type=radio]:checked + label {
        color: #EDEDED !important;
        animation: glow 1s 1 ease;   

    }
    input[type=radio]:not(:disabled) + label:hover {
        cursor: pointer;
        color: #EDEDED;
    }
    label{
        font-size: 50px;
        user-select: none;
        color: #ededed5e;
        transition: all 0.5s;
    }
}
@keyframes glow {
    0%{
        text-shadow: 10px 0 5px red,
        -10px 0 5px blue;
    }
}