.name{
    font-size: 1.5em;
    width: 100%;
    input{
        margin-top: 20px;
        width: 100%;
        padding: 10px 5px;
        font-size: 1em;
        outline: none;
        border: none;
        background-color: transparent;
        border-bottom: 2px solid #444444;
        color: #444444;
        transition: all 0.5s;
        &:focus{
            border-bottom: 2px solid #DA0037;
        }
    }
}